import './landingPage.css';
import template from './landingPage.template';

const landingPage = () => {
  let landingPageContent = document.createElement('div');
  landingPageContent.classList += 'landing-page-content';
  landingPageContent.id = 'landingPage';
  document.querySelector('body').appendChild(landingPageContent);
  landingPageContent = document.querySelector('.landing-page-content');
  landingPageContent.insertAdjacentHTML('beforeend', template);
};

export default landingPage;
