import $ from 'jquery';
import createMonster from './createMonster';
import template from './monsterFall.template';

const heroAtack = (gameState) => {
  gameState.currentState.monsterHP -= 10;
  const health = `${gameState.currentState.monsterHP}%`;
  document.getElementById('monster-progress-bar').style.setProperty('width', health, '');
  if (gameState.currentState.monsterHP <= 0) {
    gameState.currentState.countOfWonMonsters += 1;
    const contentEl = document.querySelector('body');
    contentEl.insertAdjacentHTML('beforeend', template);
    $('#monsterFallCenter').modal({});
    createMonster(gameState);
  }
};
export default heroAtack;
