import checkLoad from './checkLoad';

// disable lint for resources - some magic of checkLoad()
const animationPlayerHeal = () => new Promise((resolve) => {
  const canvas = document.getElementById('canvas');
  const ctx = canvas.getContext('2d');
  checkLoad();

  function init() {
    let i = 0;
    setInterval(() => {
      if (i >= 5) {
        return;
      } else { // eslint-disable-line
        i += 1;
      }
      ctx.clearRect(100, 50, 310, 142);
      // eslint-disable-next-line
      ctx.drawImage(resources.get('./src/screens/img/animation/angelSprite.png'), (207 * i), 0, 207, 95, 100, 50, 310, 142);
    }, 300);
  }
  // eslint-disable-next-line
  resources.load ([
    './src/screens/img/animation/angelSprite.png',
  ]);
  // eslint-disable-next-line
  resources.onReady(init);
  resolve();
});

export default animationPlayerHeal;
