import $ from 'jquery';

import randomWord from './vocabulary';

const translate = (gameState) => {
  const word = randomWord();
  gameState.currentState.correctAnswer = word.translate;
  const task = word.word;
  const insertTemplate = `
      <h4 class="task border"> ${task}  </h4>
      <input type="text" name="answer" id="taskAnsw" placeholder=" ">`;
  $('#modalBodyTask').html(insertTemplate);
  $('#taskModalTitle').html(' ');
  $('#taskModal').modal({});
  return new Promise((resolve) => {
    $('#answer').on('click', () => {
      const answerPlayer = ($('#taskAnsw').val());
      resolve(answerPlayer);
    });
  });
};

export default translate;
