import checkLoad from './checkLoad';

// disable lint for resources - some magic of checkLoad()
const animationPlayerAtack = () => new Promise((resolve) => {
  const canvas = document.getElementById('canvas');
  const ctx = canvas.getContext('2d');
  checkLoad();

  function init() {
    let i = 0;
    setInterval(() => {
      if (i >= 320) {
        ctx.clearRect(670, 250, 144, 48);
        return;
      } else {  // eslint-disable-line
        i += 10;
      }
      ctx.clearRect((340 + i), 250, 144, 48);
      // eslint-disable-next-line
      ctx.drawImage(resources.get('./src/screens/img/animation/fireball.png'), 0, 0, 432, 144, (350 + i), 250, 144, 48);
    }, 16);
  }

  resources.load ([  // eslint-disable-line
    './src/screens/img/animation/fireball.png',
  ]);
  resources.onReady(init);  // eslint-disable-line
  resolve();
});

export default animationPlayerAtack;
