import './saveResult.css';

const saveResult = (gameState) => {
  const oldScore = JSON.parse(localStorage.getItem('score')) || [];
  oldScore.push([gameState.playerName, gameState.currentState.countOfWonMonsters]);
  oldScore.sort((a, b) => b[1] - a[1]);
  const result = JSON.stringify(oldScore.slice(0, 6));
  localStorage.setItem('score', result);

  const presentScore = JSON.parse(localStorage.getItem('score'));
  const contentEl = document.querySelector('#content');
  contentEl.innerHTML = '';
  contentEl.innerHTML = '<table class="final-score" id="finalScore"></table>';
  const table = document.getElementById('finalScore');
  table.innerHTML = `<thead>
  <tr>
    <th scope="col"></th>
    <th scope="col"> </th>
  </tr>
  </thead>
  <tbody>`;
  presentScore.forEach((el) => {
    const tr = document.createElement('tr');
    tr.innerHTML = `<td>${el[0]}</td><td>${el[1]}</td>`;
    table.appendChild(tr);
  });
  table.innerHTML += '</tbody>';
  const button = document.createElement('button');
  button.classList += 'button-replay-game';
  document.querySelector('#content').appendChild(button);
  document.querySelector('.button-replay-game').innerHTML = '  ';
  document.querySelector('.button-replay-game').addEventListener('click', () => {
    alert("Sorry.Work in progress ;)");
  });
};
export default saveResult;
