import checkLoad from './checkLoad';

// disable lint for resources - some magic of checkLoad()
const animationEnemyAtack = () => {
  const canvas = document.getElementById('canvas');
  const ctx = canvas.getContext('2d');
  checkLoad();

  function init() {
    let i = 0;
    setInterval(() => {
      if (i >= 320) {
        ctx.clearRect(350, 250, 144, 48);
        return;
      } else {  // eslint-disable-line
        i += 10;
      }
      ctx.clearRect((680 - i), 250, 144, 48);
      // eslint-disable-next-line
      ctx.drawImage(resources.get('./src/screens/img/animation/fireballEnemy.png'), 0, 0, 432, 144, (670 - i), 250, 144, 48);
    }, 16);
  }

  // eslint-disable-next-line
  resources.load ([
    './src/screens/img/animation/fireballEnemy.png',
  ]);
  // eslint-disable-next-line
  resources.onReady(init);
};

export default animationEnemyAtack;
