import $ from 'jquery';


const arithmetic = (gameState) => {
  const operators = ['+', '-', '*'];
  const firstNumber = Math.floor(Math.random() * 101);
  const secondNumber = Math.floor(Math.random() * 101);
  const currentOperator = operators[Math.floor(Math.random() * 3)];
  const task = `${firstNumber} ${currentOperator} ${secondNumber} `;
  // eslint-disable-next-line
  const answer = eval(task);
  // eslint-disable-next-line
  gameState.currentState.correctAnswer = answer;
  const insertTemplate = `
      <h4 class="task border"> ${task} = </h4>
      <input type="text" name="answer" id="taskAnsw" placeholder=" ">`;
  $('#modalBodyTask').html(insertTemplate);
  $('#taskModalTitle').html('  ?');
  $('#taskModal').modal({});
  return new Promise((resolve) => {
    $('#answer').on('click', () => {
      const answerPlayer = +($('#taskAnsw').val());
      resolve(answerPlayer);
    });
  });
};

export default arithmetic;
