import saveResult from './saveResult';

const heroDefend = (gameState) => {
  gameState.currentState.playerHP -= 10;
  const health = `${gameState.currentState.playerHP}%`;
  document.getElementById('player-progress-bar').style.setProperty('width', health, '');
  if (gameState.currentState.playerHP <= 0) {
    saveResult(gameState);
  }
};
export default heroDefend;
