import arithmetic from '../tasks/arithmetic/arithmetic';
import translate from '../tasks/translate/translate';
import heroAtack from './heroAtack';
import heroDefend from './heroDefend';
import heroHeal from './heroHeal';
import animationPlayerAtack from './animationPlayerAtack';
import animationEnemyAtack from './animationEnemyAtack';
import animationPlayerHeal from './animationPlayerHeal';
import pause from './pause';

const performCast = async (id, gameState) => {
  if (id === 'atack') {
    const answerPlayer = await arithmetic(gameState);
    if (gameState.currentState.correctAnswer === answerPlayer) {
      await pause(500);
      await animationPlayerAtack();
      await pause(500);
      heroAtack(gameState);
    } else {
      await pause(500);
      animationEnemyAtack();
      await pause(500);
      heroDefend(gameState);
    }
  }
  if (id === 'heal') {
    let answerPlayer = await translate(gameState);
    answerPlayer = answerPlayer.toLowerCase();
    if (gameState.currentState.correctAnswer.indexOf(answerPlayer) !== -1) {
      await pause(500);
      await animationPlayerHeal();
      await pause(500);
      heroHeal(gameState);
    } else {
      await pause(500);
      animationEnemyAtack();
      heroDefend(gameState);
    }
  }
};
export default performCast;
