import $ from 'jquery';

import template from './chosePlayername.template';
import './chosePlayername.css';

const chosePlayerName = () => {
  $('.navbar').hide();
  $('.landing-page-content').hide();
  const contentEl = document.querySelector('#content');
  contentEl.innerHTML = template;
  return new Promise((resolve) => {
    $('.js-button').on('click', (e) => {
      e.preventDefault();
      const playerName = $('#choosePlayerName .js-player-name').val();
      resolve(playerName);
    });
  });
};
export default chosePlayerName;
