import checkLoad from '../../components/utils/checkLoad';

// disable lint for resources - some magic of checkLoad()
const getRandom = () => Math.floor(Math.random() * 3);

const heads = [
  './src/screens/img/monster/1_ORK_head.png',
  './src/screens/img/monster/2_ORK_head.png',
  './src/screens/img/monster/3_ORK_head.png',
];
const leftArm = [
  './src/screens/img/monster/1_ORK_left arm.png',
  './src/screens/img/monster/2_ORK_left arm.png',
  './src/screens/img/monster/3_ORK_left arm.png',
];
const rightArm = [
  './src/screens/img/monster/1_ORK_right arm.png',
  './src/screens/img/monster/2_ORK_right arm.png',
  './src/screens/img/monster/3_ORK_right arm.png',
];
const leftLag = [
  './src/screens/img/monster/1_ORK_left leg.png',
  './src/screens/img/monster/2_ORK_left leg.png',
  './src/screens/img/monster/3_ORK_left leg.png',
];
const rightLag = [
  './src/screens/img/monster/1_ORK_right leg.png',
  './src/screens/img/monster/2_ORK_right leg.png',
  './src/screens/img/monster/3_ORK_right leg.png',
];
const body = [
  './src/screens/img/monster/1_ORK_body.png',
  './src/screens/img/monster/2_ORK_body.png',
  './src/screens/img/monster/3_ORK_body.png',
];

const drawCharacters = (ctx) => {
  const randomHead = heads[getRandom()];
  const randomLeftArm = leftArm[getRandom()];
  const randomRightArm = rightArm[getRandom()];
  const randomLeftLag = leftLag[getRandom()];
  const randomRightLag = rightLag[getRandom()];
  const randomBody = body[getRandom()];
  checkLoad();
  // eslint-disable-next-line
  resources.load([
    './src/screens/img/hero/hero.png',
    randomHead,
    randomLeftArm,
    randomRightArm,
    randomLeftLag,
    randomRightLag,
    randomBody,
  ]);
  // eslint-disable-next-line
  resources.onReady(init);
  function init() {
    ctx.clearRect(0, 0, 1200, 600);
    // eslint-disable-next-line
    ctx.drawImage(resources.get('./src/screens/img/hero/hero.png'), 100, 150, 423, 438);
    ctx.save();
    ctx.scale(-1, 1);
    ctx.drawImage(resources.get(randomRightLag), -1000, 450, 72, 107);  // eslint-disable-line
    ctx.drawImage(resources.get(randomLeftLag), -930, 450, 67, 103);  // eslint-disable-line
    ctx.drawImage(resources.get(randomLeftArm), -900, 290, 121, 213); // eslint-disable-line
    ctx.drawImage(resources.get(randomBody), -1050, 230, 220, 251); // eslint-disable-line
    ctx.drawImage(resources.get(randomRightArm), -1120, 290, 127, 210); // eslint-disable-line
    ctx.drawImage(resources.get(randomHead), -1050, 150, 233, 209); // eslint-disable-line
    ctx.restore();
  }
};

export default drawCharacters;
