import monsterName from './monsterName';
import drawCharacters from '../../screens/battle/drawCharacters';

const createMonster = (gameState) => {
  gameState.currentState.monsterHP = 100;
  document.querySelector('.js-monster-name').textContent = monsterName();
  document.getElementById('monster-progress-bar').style.setProperty('width', '100%', '');
  const canvas = document.getElementById('canvas');
  const ctx = canvas.getContext('2d');
  drawCharacters(ctx);
};
export default createMonster;
