import template from './battle.template';
import modalDialog from '../../components/modal-dialog-cast/modal-dialog';
import drawCharacters from './drawCharacters';
import templateModalTask from '../../components/tasks/arithmetic/arithmetic.template';

import './battle.css';

const battle = () => {
  const contentEl = document.querySelector('#content');
  contentEl.innerHTML = template;
  document.querySelector('body').style.backgroundImage = 'url(./src/screens/img/background/forestBackground.jpg)';
  document.querySelector('body').style.backgroundSize = 'cover';
  const el = document.createElement('canvas');
  el.id = 'canvas';
  el.classList = 'canvas-content';
  el.setAttribute('width', '1200');
  el.setAttribute('height', '600');
  contentEl.appendChild(el);
  const canvas = document.getElementById('canvas');
  const ctx = canvas.getContext('2d');
  const contentElement = document.querySelector('body');
  contentElement.insertAdjacentHTML('beforeend', templateModalTask);
  drawCharacters(ctx);
  modalDialog();
};

export default battle;
